import React from 'react';
import styled from 'styled-components';
import FirstPhoto from '../../assets/img/portfolio/1-photo.jpg'
import SecondPhoto from '../../assets/img/portfolio/2-photo.jpg'
import ThirdPhoto from '../../assets/img/portfolio/3-photo.jpg'
import ForthPhoto from '../../assets/img/portfolio/4-photo.jpg'
import FifthPhoto from '../../assets/img/portfolio/5-photo.jpg'
import SixthPhoto from '../../assets/img/portfolio/6-photo.jpg'
import SeventhPhoto from '../../assets/img/portfolio/7-photo.jpg'
import EightPhoto from '../../assets/img/portfolio/8-photo.jpg'
import NinthPhoto from '../../assets/img/portfolio/9-photo.jpg'
import TenthPhoto from '../../assets/img/portfolio/10-photo.jpg'
import EleventhPhoto from '../../assets/img/portfolio/11-photo.jpg'
import TwelwthPhoto from '../../assets/img/portfolio/12-photo.jpg'
import Photo13 from '../../assets/img/portfolio/13-photo.jpg'
import Photo14 from '../../assets/img/portfolio/14-photo.jpg'
import Photo15 from '../../assets/img/portfolio/15-photo.jpg'
import Photo16 from '../../assets/img/portfolio/16-photo.jpg'
import Photo17 from '../../assets/img/portfolio/17-photo.jpg'
import Photo18 from '../../assets/img/portfolio/18-photo.jpg'




const Section = styled.section`
   margin: 0 auto;
  @media (min-width: 1024px){
   max-width: 1024px;
    
  }
`

const Title = styled.h2`
   font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 3em;
    color: #212529;
    text-align: center;
`

const Gallery = styled.div`
   display: flex;
   flex-wrap: wrap;
   justify-content: space-evenly;
`

const PhotoWrapper = styled.div`
  max-width: 100%;
  padding: 0 10px;
  @media(min-width: 768px){
  flex-basis: 45%;
  align-items: center;
  }
  @media(min-width: 1024px){
  flex-basis: 30%;
  }
`

const Photo = styled.img`
  @media(min-width: 320px){
  display: inline-block;
  width: 100%;
  }
  @media(min-width: 768px){
  }
`

export default function Galery() {
    return (
        <Section>
            <Title>GALERIA NASZE PRACE:</Title>
            <Gallery>
                <PhotoWrapper><Photo src={FirstPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={SecondPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={ThirdPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={ForthPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={FifthPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={SixthPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={SeventhPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={EightPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={NinthPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={TenthPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={EleventhPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={TwelwthPhoto} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={Photo13} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={Photo14} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={Photo15} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={Photo16} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={Photo17} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={Photo18} alt="somepicture"/></PhotoWrapper>
            </Gallery>

        </Section>
    )
}


