export default function Price() {
    return (
        <Section>
            <Wrapper>
            <Title>cennik</Title>
                <SectionTitle>OBUWIE</SectionTitle>
                <SectionWrapper>
                    {/*Boots Start*/}
                    {/*Price Block*/}
            <PriceBlock>
                <PriceTitle>CZYSZCZENIE, ODŚWIEŻENIE BUTÓW</PriceTitle>
                <PriceText><strong>Podstawowe czyszczenie butów</strong> lekkimi środkami, zastosowanie nowych past, wosków, renowatora koloru dla zamszu, nubuka. (UWAGA, nie obejmuje: zniwelowanie przebarwień skóry, usunięcie zabrudzeń z tłuszczu, usuwanie plam, uzupełnienie ubytków, pęknięć i uszkodzeń lica, usunięcie starych past i wosków z porów skóry, usunięcie przetarć i przebarwień skóry, impregnacje)</PriceText>
                <PriceText><strong>DO KOSTKI - od 40 zl</strong></PriceText>
                <PriceText><strong>ZA KOSTKĘ - od 60 zl</strong></PriceText>
                <PriceText><strong>KOZAKI- od 80 zł</strong></PriceText>
            </PriceBlock>
                    {/*Price Block*/}
             <PriceBlock>
                <PriceTitle>RENOWACJA</PriceTitle>
                 <PriceText>Kompleksowe czyszczenie buta, regeneracja uszkodzeń (usuniencze śladuw użytkowania, zaciekow solnych, rys, spękan i przetarc na skórze, likwidacja plam, usunięcie starych past i wosków z porów skóry, usunięcie przetarć i przebarwień skóry), ponawianie kolorów skóry i podeszwy, impregnacja.</PriceText>
                 <PriceText><strong>DO KOSTKI - od 90 zl</strong></PriceText>
                 <PriceText><strong>ZA KOSTKĘ - od 100 zl</strong></PriceText>
                 <PriceText><strong>WYSOKI - od 120 zl</strong></PriceText>
             </PriceBlock>
                    {/*Price Block*/}
            <PriceBlock>
                <PriceTitle>RENOWACJA ZE ZMIANĄ KOLORU</PriceTitle>
                <PriceText><strong>Renowacija ze zmianą koloru  -</strong> zawiera pełny zakres usługi renowacji, oraz zmianę kolorów. Skóra zamszowa, nubukowa możliwa przemiana koloru na kolor ciemniejszy niż był pierwotnie.</PriceText>
                <PriceText><strong>DO KOSTKI - od 90 zł</strong></PriceText>
                <PriceText><strong>ZA KOSTKĘ - od 120 zł</strong></PriceText>
                <PriceText><strong>WYSOKI – od 150 zł</strong></PriceText>
            </PriceBlock>
                    {/*Price Block*/}
            <PriceBlock>
                <PriceTitle>PERSONALIZACJA/CUSTOM</PriceTitle>
                <PriceText><strong>PERSONALIZACJA\CUSTOM -</strong> indywidualny projekt butów że by podkreślić swoją indywidualność. Zawiera pełny zakres usług renowacji ze zmianą koloru całego butu czy oddzielnych elementów plus naniesienie indywidualnego rysunku czy napisu.</PriceText>
                <PriceText><strong>DO KOSTKI - od 120 zl</strong></PriceText>
                <PriceText><strong>ZA KOSTKE - od 140 zl</strong></PriceText>
                <PriceText><strong>WYSOKI - od 160 zl</strong></PriceText>
             </PriceBlock>
                    {/*Price Block*/}
             <PriceBlock>
                <PriceTitle>MALOWANIE WYBRANYCH ELEMENTOW</PriceTitle>
                 <PriceText><strong>Malowanie wybranych elementow  -</strong> malowanie podeszwy lub dowolnego elementu obuwia w jeden kolor (bez zmiany koloru), pod warunkiem, że objętość malowania nie więcej 25% ogólnej powierzchni obuwia.</PriceText>
                 <PriceText><strong>JEDEN ELEMENT - od 30 zl</strong></PriceText>
             </PriceBlock>
                    {/*Price Block*/}
            <PriceBlock>
                <PriceTitle>DODATKOWO</PriceTitle>
                <PriceText><strong>Komplet usług szewskich - </strong>Fleki, Zelówki, Zapiętki, Rozciąganie butów, Wypełnienie zapadniętych pięt, Obniżenie obcasów, Wymiana obcasów, Wymiana pasków/rzepów/klamerek, Wymiana spodów, Wszycie ekspresu/suwaka, Wkladki, Klejenie, Szycie buta.</PriceText>
                <PriceText><strong>Impregnacja obuwia  -</strong> niewidoczny szar ochronny, który zabezpiecza buty przed deszczem, śniegiem, plamami z błota, przemiękaniem, plamami po wylaniu soku, itp.</PriceText>
                <PriceText><strong>IMPREGNACJA - 10 zł</strong></PriceText>
                </PriceBlock>
                    {/*Boots Finished*/}
            </SectionWrapper>


                <SectionTitle>TORBY I INNE NIE DUŻE WYROBY
                </SectionTitle>
                    <SectionWrapper>
                        {/*Bags Renew*/}

                <PriceBlock>
                        <PriceTitle>RENOWACJA SKÓRZANYCH TOREBEK </PriceTitle>
                        <PriceText>
                            <strong>Renowacja skórzanych torebek</strong> - kompleksowe czyszczenie, regeneracja uszkodzeń (usunięcie śladów użytkowania, zacieków, rys, spękań i przetarć na skórze, likwidacja plam) przywracanie kolorów.
                        </PriceText>
                    <PriceText><strong>MAŁA DO 35 cm – od 60 zl</strong></PriceText>
                    <PriceText><strong>ŚREDNIA DO 55 cm – od 90 zl</strong></PriceText>
                    <PriceText><strong>DUŻA POWYŻEJ 55 cm – od 120 zl</strong></PriceText>
                    <PriceText><strong>PERSONALIZACJA/CUSTOM – od 150 zl</strong></PriceText>
                    <PriceText><strong>Usługi kaletnicze -</strong> Wymiana rączek, ekspresu, nap.</PriceText>
                </PriceBlock>
              </SectionWrapper>

                    {/*Lether staff*/}
                    <SectionTitle>RENOWACJA SKÓRZANEJ ODZIEŻY</SectionTitle>
                <SectionWrapper>
                  <PriceBlock>
                      <PriceText>
                      <strong>Renowacja</strong> - kompleksowe czyszczenie, regeneracja uszkodzeń (usunięcie śladów użytkowania, zacieków, rys, spękań i przetarć na skórze, likwidacja plam) przywracanie kolorów.
                          </PriceText>
                          <PriceText><strong>KAMIZELKI, KOSZULI - od 80 zł</strong></PriceText>
                      <PriceText><strong>SPODNI, SPÓDNICE – od 90 zł</strong></PriceText>
                      <PriceText><strong>KURTKI, PŁASZCZE - od 180 zł</strong></PriceText>
                      <PriceText><strong>KOŻUCHY– od 200 zł</strong></PriceText>
                      <PriceText><strong>Kurtki motoczyklowe - od 190 zł</strong></PriceText>
                      <PriceText><strong>Kombinezon motocyklowy - od 250 zł</strong></PriceText>
                  </PriceBlock>
              </SectionWrapper>
                <SectionTitle>TAPICERKI SAMOCHODOWEJ ORAZ MEBLI</SectionTitle>
                <SectionWrapper>
                    <PriceText><strong>RENOWACJA TAPICERKI SAMOCHODOWEJ ORAZ MEBLI</strong> - kompleksowe czyszczenie, regeneracja uszkodzeń (usunięcie śladów użytkowania, zacieków, rys, spękań i przetarć na skórze, likwidacja plam, naprawa dziur, zadrapań, rozdarć) przywracanie kolorów.</PriceText>
                    <SmallWrapper>
                        <FlexElement>
                            <PriceText>Czyszczenie i konserwacja wnętrz pojazdów (<strong>tapicerka skórzana</strong>) <strong>od 300 zł</strong></PriceText>
                            <PriceText>Czyszczenie i konserwacja wnętrz pojazdów (<strong>kompleksowe przygotowanie samochodu do sprzedaży</strong>) <strong>od 400 zł</strong></PriceText>
                            <PriceText>Renowacja skórzanej tapicerki samochodowej <strong>jeden fotel - od 250 zł</strong></PriceText>
                            <PriceText>Renowacja skórzanej tapicerki samochodowej <strong>tylna kanapa - od 400 zł</strong></PriceText>
                            <PriceText>Renowacja skórzanych <strong>boczków od 80 zł</strong></PriceText>
                            <PriceText>Renowacja skórzanej <strong>kierownicy od 100 zł</strong></PriceText>
                        </FlexElement>
                        <FlexElement>
                            <PriceText>Czyszczenie i konserwacja skórzanej tapicerki meblowej (<strong>fotel</strong>) <strong>od 70 zł</strong></PriceText>
                            <PriceText>Czyszczenie i konserwacja skórzanej tapicerki meblowej (<strong>kanapa</strong>) <strong>od 150 zł</strong></PriceText>
                            <PriceText>Czyszczenie i konserwacja skórzanej tapicerki meblowej (<strong>narożnik\wypoczynek</strong>) <strong>od 250 zł</strong></PriceText>
                            <PriceText>Renowacja skórzanej tapicerki meblowej (<strong>fotel</strong>) <strong>od 300 zł</strong></PriceText>
                            <PriceText>Renowacja skórzanej tapicerki meblowej (<strong>kanapa</strong>) <strong>od 500 zł</strong></PriceText>
                            <PriceText>Renowacja skórzanej tapicerki meblowej (<strong>narożnik\wypoczynek</strong>) <strong>od 800 zł</strong></PriceText>
                        </FlexElement>
                    </SmallWrapper>
                </SectionWrapper>
            </Wrapper>
        </Section>
    )
}


