import React from 'react';
import {NavLink} from 'react-router-dom';
import styled from 'styled-components';
import Logo from '../../assets/img/Skor_Keeper_header_logo.png';

const NavMenu = styled.div`
    @media (min-width: 320px){
    position: relative;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1030;
    border: none;
    background: #2C3E50;
    padding: 10px 15px;
    font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    box-sizing: padding-box;
    margin: 0 auto;
    }
    @media (min-width: 480px) {
    position: sticky;
    display: flex;
    justify-content: space-between;
    }
    @media (min-width: 768px){
    height: 6vh;
    }
    @media (min-width: 1440px){
    padding: 15px 208px;
    }
    
`

const MenuLogoWrapper = styled.div`
    @media(min-width: 320px){
    display: none;
    }
    @media(min-width: 768px){
    display: block;
    
    }
`

const MenuLogoImg = styled.img`
    display: inline-block;
    height: 45px;
`

const MenuList = styled.ul`
    @media (min-width: 320px){
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    
    }
    @media (min-width: 480px){
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
    }
    @media (min-width: 768px) {
    justify-content: flex-end;
    align-items: center;
    }
`

const LinkContainer = styled.li`
@media (min-width: 320px){
    display: block;
    
    }
    @media (min-width: 480px){
      border-bottom: none;
      width: content-box;
      display: inline-block;
    }
`



const MenuItem = styled(NavLink)`
  @media (min-width: 320px){
  min-width: 100%;
  display: inline-block;
  padding: 7px 8px;
  text-align: center;
  border-bottom: 1px solid #191d23;
  box-sizing: border-box;
  color: white;
  
  :active{
    border-radius: 3px;
    background: #18BC9C;
    }
  }
  @media (min-width: 480px){
  border-bottom: none;
  }
  @media (min-width: 1024px){
  font-size: 18px;
  }
`

export default function NavBar() {
        return (
                <NavMenu>
                    <MenuLogoWrapper href="#">
                        <MenuLogoImg src={Logo} alt="pict-upper"/>
                    </MenuLogoWrapper>
                    <MenuList>
                        <LinkContainer>
                            <MenuItem to="/about">O NAS</MenuItem>
                        </LinkContainer>
                        <LinkContainer>
                            <MenuItem to="/price">CENNIK</MenuItem>
                        </LinkContainer>
                        <LinkContainer>
                            <MenuItem to="/work">JAK PRACUJEMY</MenuItem>
                        </LinkContainer>
                        <LinkContainer>
                            <MenuItem to="/gallery">GALERIA</MenuItem>
                        </LinkContainer>
                    </MenuList>
                </NavMenu>
        )
    }


