import React from 'react';
import styled from 'styled-components';
import p1 from '../../../assets/img/portfolio/Wear/1.jpg'
import p2 from '../../../assets/img/portfolio/Wear/2.jpg'
import p3 from '../../../assets/img/portfolio/Wear/3.jpg'
import p4 from '../../../assets/img/portfolio/Wear/4.jpg'
import p5 from '../../../assets/img/portfolio/Wear/5.jpg'
import p6 from '../../../assets/img/portfolio/Wear/6.jpg'
import p7 from '../../../assets/img/portfolio/Wear/7.jpg'
import p8 from '../../../assets/img/portfolio/Wear/8.jpg'
import p9 from '../../../assets/img/portfolio/Wear/9.jpg'
import p10 from '../../../assets/img/portfolio/Wear/10.jpg'
import p11 from '../../../assets/img/portfolio/Wear/11.jpg'
import p12 from '../../../assets/img/portfolio/Wear/12.jpg'
import p13 from '../../../assets/img/portfolio/Wear/13.jpg'
import p14 from '../../../assets/img/portfolio/Wear/14.jpg'
import p15 from '../../../assets/img/portfolio/Wear/15.jpg'
import p16 from '../../../assets/img/portfolio/Wear/16.jpg'
import p17 from '../../../assets/img/portfolio/Wear/17.jpg'
import p18 from '../../../assets/img/portfolio/Wear/18.jpg'
import p19 from '../../../assets/img/portfolio/Wear/19.jpg'




const Section = styled.section`
   margin: 0 auto;
   background: #B2C5D2;
   @media(min-width: 320px){
    width: 320px;
   }
   @media(min-width: 425px){
   width: 420px;
   }
   @media(min-width: 760px){
    width: 760px;
   }
   
  @media (min-width: 1024px){
   max-width: 1024px;
  }
`

// const Title = styled.h2`
//    font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
//     font-weight: 700;
//     text-transform: uppercase;
//     font-size: 3em;
//     color: #212529;
//     text-align: center;
// `

const MainImg = styled.div`
    @media (min-width: 320px){
    display: block;
    margin: 0, auto;
    }
    width: 100%;
    max-height: 600px;
`
const TopImg = styled.img`
    @media(min-width: 320px){
    height: 200px;
    max-width: 100%;
    margin: 0 auto;
    display: block;
    }
    @media(min-width: 425px){
   height: 250px;
   }
   @media(min-width: 768px){
   height: 400px;
   }
    
`


const Gallery = styled.div`
   display: grid;
   grid-template-columns: repeat(4, 1fr);
   grid-gap: 5px;
   padding: 0 5px;
   background: #B2C5D2;
;
`

const PhotoWrapper = styled.div`
  max-width: 100%;
  border: 2px, solid, #333;
`

const Photo = styled.img`
  @media(min-width: 320px){
  display: block;
  max-width: 100%;
  }
`

export default function Galery() {
    return (
        <Section>
            {/*Main img here*/}
            <MainImg>
                <TopImg src={p1}></TopImg>
            </MainImg>
            {/*Rest of the gallery here*/}
            <Gallery>
                <PhotoWrapper><Photo src={p1} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p2} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p3} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p4} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p5} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p6} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p7} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p8} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p9} alt="somepicture"/></PhotoWrapper>
                <PhotoWrapper><Photo src={p10} alt="somepicture"/></PhotoWrapper>

            </Gallery>

        </Section>
    )
}


