import React, { Component } from 'react';
import MainLogo from '../../assets/img/Skor_Keeper_logo.png'
import styled from 'styled-components';
import Background from '../../assets/img/portfolio/shoeshine.jpg'


//Header styles
const ContainerHeader = styled.div`
    width: 100%;  
    font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 400;
    font-size: 20px;
    @media(min-width: 320px){
    padding: 20px 0; 
    background-color: #f4f4f4;
    }
    @media(min-width: 768px){
    padding: 50px 0 23px;
    background-color: transparent;
    }
`

const HeaderWrapper = styled.div`
    max-width: 1024px;
    margin: 0 auto;
`

const Wrapper = styled.div`
    width: 100%;
    @media(min-width: 768px){
    background-color: white;
    }
`

const HeaderBackground = styled.img`
  @media(min-width: 320px){
  display: none;
  }
  @media(min-width: 768px){
  display: block;
    width: 100%;
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;   
    vertical-align: middle; 
    }
    @media(min-width: 1024px){
    top: -92px;
    }
`

const HeaderLogo = styled.img`
  margin: 0 auto;
  display: block;
  @media(min-width: 320px){
  height: 134px;
  margin: 0 auto;
  }
  @media(min-width: 768px){
  height: 170px;
  }
  @media(min-width: 1024px){
  height: 200px;
  }
  @media(min-width: 1024px){
  height: 230px;
  }
`

const HeaderText = styled.p`
  display: block;
  width: 79%;
  margin: 4vh auto 2vh;
  text-align: center;
  @media(min-width: 768px){
   color: white;
   font-size: 28px;
  }
`

//Content styles

const Section = styled.section`
  width: 100vw;
`

const ContentWrapper = styled.div`
max-width: 1024px;
margin: 0 auto;
  @media (min-width: 320px){
  padding: 10px 10px;
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  
  }
    @media (min-width: 768px){
  text-align: left;
  padding: 30px 10px;
}
`

const Title = styled.h2`
    font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 200;
    text-transform: uppercase;
    line-height: 50px;
    background-color: #f4f4f4;
    display: block;
    color: #212529;
    
    @media(min-width: 320px){
    text-align: center;
    font-size: 36px;
    }
    @media(min-width: 768px){
        font-size: 46px;
        display: inline-block;
        text-align: left;
    }
    @media(min-width: 1024px){
    font-size: 56px;
    }
`

export default class About extends Component {
    render(){

        return(
            <section>
                <ContainerHeader>
                    <HeaderBackground src={Background} alt="there suppose to be a picture"/>
                    <HeaderWrapper>
                    <HeaderLogo src={MainLogo} alt=""/>
                    <HeaderText>DAJ SZANSE SWOIM ULUBIONYM SKÓRZANYM RZECZOM ŻYĆ NA NOWO!</HeaderText>
                    </HeaderWrapper>
                </ContainerHeader>
                <Section>
                    <Wrapper>
                        <ContentWrapper>
                            <Title>O NAS:</Title>
                            <p>Masz ulubioną rzecz ze skóry, lecz ona utraciła swój wygląd? Zrobiła się brudna,
                                podrapana, otarła się, wypłowiała, nie ma dawnego koloru? „Skór Keeper” pomoże ci ją
                                odnowić!</p>
                        </ContentWrapper>
                    </Wrapper>
                    <Wrapper>
                        <ContentWrapper>
                            <Title>CO ROBIMY:</Title>
                            <p>- Czyszczenie i renowacja wyrobów ze skóry za pomocą ekskluzywnych technologii dla zamszu, nubuku i gładkiej skóry;</p>
                            <p>- Usuwamy ślady użytkowania, rysy, spękania, przetarcia na skórze, zacieki solne;</p>
                            <p>- Naprawiamy dziury, zadrapania, rozdarcia;</p>
                            <p>- Odświeżamy skórę, przywracamy lub zmieniamy kolor;</p>
                            <p>- Impregnujemy i konserwujemy skóry;</p>
                            <p>- Czyszczenie i konserwacja wnętrz pojazdów (tapicerkai skórzana);</p>
                            <p>- Renowacja skórzanej tapicerki samochodowej oraz meblowej (naprawa, szyczie, zmiana naturalnej skóry lub ekoskóry, farbowanie);</p>
                            <p>- Renowacja i obszycie kierownicy skórą;</p>
                            <p>- Odwiedzając Skór Keeper możecie nie tylko oddać rzeczy do renowacji ale również skorzystać z kompletu usług szewskich i pralni chemicznej .</p>
                        </ContentWrapper>
                    </Wrapper>
                    <Wrapper>
                        <ContentWrapper>
                            <Title>JAKIE WYROBY:</Title>
                            <p><b>Skorzana tapicerka: Samochodowa oraz Meblowa, Kierownica;</b></p>
                            <p><b>Obuwie:</b> Trampki, Sneakersy, Półbuty, Kozaki, Timberlamd, Emu, Uggi, Szpilki, Baletki, Pantofle, Mokasyny, Sandały, i inne.</p>
                            <p><b>Torby</b>, paski, portfele, portmonetki, rękawiczki, czapki.</p>
                            <p><b>Duże wyroby:</b> Kamizelki, kurtki, płaszcze, kożuchy, spódnice, spodnie.</p>
                        </ContentWrapper>
                    </Wrapper>
                </Section>
            </section>
        )
    }
}


