import React from 'react';
import styled from 'styled-components';
import FaHome from 'react-icons/lib/fa/map-marker';
import FaClock from 'react-icons/lib/fa/clock-o';
import FaPhone from 'react-icons/lib/fa/phone-square';
import FaMail from 'react-icons/lib/fa/envelope';

const home = <FaHome/>;
const clock = <FaClock/>;
const phone = <FaPhone/>;
const mail = <FaMail/>

const Section = styled.section`
    width: 100%;
    //&:last-child{
    //  background-color: rgb(244, 246, 249);
    //}
`

const MapWrapper = styled.div`
  max-width: 1024px;
  margin: 10px auto;
  padding: 10px 15px;
`

const TopWrapper = styled.div`
  max-width: 1024px;
  padding: 10px 15px;
  background-color:#f4f6f9;
  margin: 10px auto;
  @media(min-width: 768px){
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
`

const BottomWrapper = styled.div`
  max-width: 1024px;
  padding: 10px 15px;
  background-color:#f4f6f9;
  margin: 10px auto;
`

const Title = styled.h2`
   font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 3em;
    color: #212529;
    text-align: center;
    //line-height: 1.1;
`

const LesserTitle = styled.h3`
    font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-weight: 900;
    text-align: center;
    font-size: 1.75em;
    line-height: 1.1em;
    text-transform: uppercase;
    color: #212529;
    
`

const SectionWrapper = styled.div`
    margin-bottom: 15px;
    @media(min-width: 768px){
    flex-basis: 39%;
    }
`

const Icon = styled.i`
  color:white;
    width: 46px;
    height: 46px;
    margin: 0 auto;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    color: #2C3E50;
`

const AdressText = styled.p`
font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;;
      color: #2C3E50;
    font-weight: bold;
    font-size: 1.25em;
    line-height: 1;
    text-align: center;
`

const SendmailText = styled.p`
  font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: normal;
    line-height: 1.5;
    color: #212529;
    margin-bottom: 1rem;
    font-weight: 400;
    font-size: 1.25em;
`

export default function HowWeWork() {
    return (
        <Section>

            <Title>CZEKAMY NA CIEBIE POD ADRESEM:</Title>
            <TopWrapper>
            <SectionWrapper>
                <Icon>{home}</Icon>
                <AdressText>RYNEK GÓRNIAK, Dom Rzemiosła, ul. Księdza Piotra Skargi 8/10,</AdressText>
                <AdressText>Łódz, 93-036</AdressText>
            </SectionWrapper>
            <SectionWrapper>
                <Icon>{clock}</Icon>
                <AdressText>Godziny pracy:</AdressText>
                <AdressText>Wtorek – Рiątek 09:00 – 17:00</AdressText>
                <AdressText>Sobota 10:00 – 14:00</AdressText>
            </SectionWrapper>
            <SectionWrapper>
                <Icon>{phone}</Icon>
                <AdressText>+48 574 174 009</AdressText>
            </SectionWrapper>
            <SectionWrapper>
                <Icon>{mail}</Icon>
                <AdressText>skorkeeper@poczta.pl</AdressText>
            </SectionWrapper>
            </TopWrapper>
            <MapWrapper>
                <iframe
                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2470.6331902699526!2d19.458542715745878!3d51.73974417967403!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x471a3528d7dcdf53%3A0xddc9fdd04c1e4297!2sSk%C3%B3r+Keeper!5e0!3m2!1suk!2sua!4v1554105332992!5m2!1suk!2sua"
                    width="100%" height="400" frameBorder="0" style="border:0" allowFullScreen>
                </iframe>
            </MapWrapper>
            <BottomWrapper>
            <div>
                <LesserTitle>JAK WYSŁAĆ WYROBY Z INNEGO MIASTA:</LesserTitle>
                <SendmailText>1. Wysyłasz zdjęcia wyrobu na adres <strong>e-mail: skorkeeper@poczta.pl.</strong></SendmailText>
                <SendmailText>Prosimy o dokładne zdjęcie. Ważnie - sfotografować wszystkie draśnięcia obtarcia, plamy. Im dokładniejsze zdjęcia i opis nam przyślesz, tym lepiej będziemy wiedzieć co możemy dokładnie dla Ciebie zrobić. Zostaw swój numer telefonu, a my się skontaktujemy i szczegółowo omówimy usługe.</SendmailText>
                <SendmailText>2. Podejmujesz decyzję i wykonujesz przelew, podaną kwotę na numer konta: <strong>60 1050 1461 1000 0092 4564 1437.</strong> PAMIĘTAJ, aby w tytule przelewu wpisać swoje IMIĘ i NAZWISKO.</SendmailText>
                <SendmailText>3. Wyrób możesz wysłać dowolnym sposobem, na nasz  <strong>adres: RYNEK GÓRNIAK, Dom Rzemiosła, ul. Księdza Piotra Skargi 8/10, Łódz, 93-036.</strong> Dodatkowo do paczki dołączasz wydrukowany, wypełniony i podpisany formularz zlecenia usługi, gdze podajesz informacje: imię i nazwisko, numer telefonu, opis usługi oraz adres, na który mamy odesłać z powrotem. Ważne! Pamiętaj, jeśli wybrana przez Ciebie usługa jest związana z zmianą koloru - przyślij razem z wyrobem wór koloru jaki oczekujesz.</SendmailText>
                <SendmailText>4. Potwierdzamy odbiór wyrobu na mail z potwierdzeniem odebrania paczki i ustalamy ostateczne szczegóły.</SendmailText>
                <SendmailText>5. Zabieramy się do pracy, po dwóch tygodniach przesyłamy odnowiony wyrób z powrotem do Ciebie.</SendmailText>
            </div>
        </BottomWrapper>
        </Section>
    )
}


