import React from 'react';
import styled from 'styled-components';
import FaFacebook from 'react-icons/lib/fa/facebook';
import Regulamin from '../../assets/Download/Regulamin.docx'
import Załączniki from '../../assets/Download/załączniki.docx'

const facebook = <FaFacebook/>
const downloadRegulamin = Regulamin;
const downloadZałączniki = Załączniki;

const FooterWrapper = styled.div`
    @media (min-width: 320px){
    color: white;
    font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    background-color: #2C3E50;
    text-align: center;
    padding: 15px 15px;
      }
    @media (min-width: 768px){
    display: flex;
    align-items: stretch;
    padding-top: 25px;
    }
    @media (min-width: 1440px){
    padding: 45px 208px;
    }
`

const LocationWrapper = styled.div `
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  @media (min-width: 768px){
    width: 33%;
  }
`

const DescriptionHeader = styled.h3`
  font-size: 26px;
  margin-bottom: 10px;
  font-weight: 700;
  text-transform: uppercase;
`

const AdressText = styled.p`
    font-family: 'Lato', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    line-height: 1.5;
    font-size: 20px;
    
`

const HeartFB = styled.a`
    color:white; 
    border: 2px solid white;
    border-radius: 50%;
    width: 46px;
    height: 46px;
    margin: 0 auto;
    font-size: 25px;
    display: flex;
    align-items: center;
    justify-content: space-around;
`

const DownloadLink = styled.a`
  color: white;
  text-decoration-line: underline;
`

export default function Footer() {
    return (
        <FooterWrapper>
            <LocationWrapper>
                <DescriptionHeader>Location</DescriptionHeader>
                <AdressText>DOM RZEMIOSŁA, Dom Rzemiosła, ul. Księdza Piotra Skargi 8/10</AdressText>
                <AdressText>Łódz, 93-036</AdressText>
            </LocationWrapper>
            <LocationWrapper>
                <DescriptionHeader>facebook</DescriptionHeader>
                <HeartFB href="https://www.facebook.com/SkorKeeper/" target="_blank">
                    {facebook}
                </HeartFB>
            </LocationWrapper>
            <LocationWrapper>
                <DescriptionHeader>REGULAMIN</DescriptionHeader>
                <AdressText>
                    Korzystanie z naszych usług oznacza akceptację regulaminu
                </AdressText>
                <AdressText>
                    Download <DownloadLink href={downloadRegulamin} download>regulamin</DownloadLink> ta <DownloadLink href={downloadZałączniki} download>załączniki</DownloadLink>
                </AdressText>
            </LocationWrapper>

        </FooterWrapper>
    )
}


